@import './_variables.scss';
.ColorBox {
	width: 20%;
	height: 25%;
	margin: 0 auto;
	display: inline-block;
	position: relative;
	cursor: pointer;
	/* margin-bottom: -3.5px; */
	vertical-align: top;
}

.ColorBox.go-back {
	background-color: black;
	color: white;
	text-decoration: none;
}

// .ColorBox--large-width {
// 	width: 50%;
// }

@media (min-width: $breakpoint-lg) {
	.singleColorPalette {
		height: 50%;
		width: 20%;
	}
	.ColorBox.go-back {
		width: 20%;
		height: 50%;
	}
}

@media (max-width: $breakpoint-lg) {
	.ColorBox {
		width: 25%;
		height: 20%;
	}
	.singleColorPalette {
		height: 33.33%;
		width: 25%;
	}
	.ColorBox.go-back {
		width: 25%;
		height: 33.33%;
	}
}

@media (max-width: $breakpoint-lg) {
	.ColorBox {
		width: 25%;
		height: 20%;
	}
	.singleColorPalette {
		height: 33.33%;
		width: 25%;
	}
	.ColorBox.go-back {
		width: 25%;
		height: 33.33%;
	}
}
@media (max-width: $breakpoint-md) {
	.ColorBox {
		width: 50%;
		height: 10%;
	}
	.singleColorPalette {
		height: 20%;
		width: 50%;
	}

	.ColorBox.go-back {
		width: 50%;
		height: 20%;
	}
}
@media (max-width: $breakpoint-xsm) {
	.ColorBox {
		width: 100%;
		height: 5%;
	}
	.singleColorPalette {
		height: 10%;
		width: 100%;
	}
	.ColorBox.go-back {
		width: 100%;
		height: 10%;
	}

	.copy-msg h1 {
		font-size: 5rem;
	}
}

.copy-button,
.back-button {
	width: 100px;
	height: 30px;
	position: absolute;
	display: inline-block;
	top: 50%;
	left: 50%;
	margin-left: -50px;
	margin-top: -15px;
	text-align: center;
	outline: none;
	background: rgba(255, 255, 255, 0.3);
	font-size: 1rem;
	line-height: 30px;
	color: white;
	text-transform: uppercase;
	border: none;
	opacity: 0;
}

.back-button {
	opacity: 1;
	text-decoration: none;
}
.ColorBox:hover .copy-button {
	opacity: 1;
	transition: 0.5s;
}

.box-content {
	position: absolute;
	padding: 10px;
	width: 100%;
	left: 0px;
	bottom: 0px;
	color: black;
	letter-spacing: 1px;
	text-transform: uppercase;
	font-size: 12px;
}

.light-text {
	color: white;
}
.ColorBox .dark-text {
	color: rgba(0, 0, 0, 0.5);
}
.see-more {
	background: rgba(255, 255, 255, 0.3);
	position: absolute;
	border: none;
	right: 0px;
	bottom: 0px;
	color: white;
	width: 60px;
	height: 30px;
	text-align: center;
	line-height: 30px;
	text-transform: uppercase;
}

.copy-overlay {
	opacity: 0;
	z-index: 0;
	width: 100%;
	height: 100%;
	transition: transform 0.6s ease-in-out;
	transform: scale(0.1);
}

.copy-overlay.show {
	opacity: 1;
	transform: scale(50);
	z-index: 10;
	position: absolute;
}

.copy-msg {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: 4rem;
	transform: scale(0.1);
	opacity: 0;
	color: white;
}

.copy-msg.show {
	opacity: 1;
	transform: scale(1);
	z-index: 25;
	transition: all 0.4s ease-in-out;
	transition-delay: 0.1s;
}

.copy-msg h1 {
	font-weight: 400;
	text-shadow: 1px 2px black;
	background: rgba(255, 255, 255, 0.2);
	width: 100%;
	text-align: center;
	margin-bottom: 0;
	padding: 1rem;
	text-transform: uppercase;
}

.copy-msg p {
	font-size: 1.5rem;
	font-weight: 100;
}
